.JobInfoComponent__container{
    display: grid;
   /* grid-template-columns: 8.75rem 2fr 1fr;*/
grid-template-columns: 1fr 2fr 1fr;
  /*margin:-4rem 15rem 0 15rem;*/
  margin-top: -4rem;
 /* padding-right: 15rem;
  padding-left: 15rem;*/
  margin-left:15rem;
  margin-right:15rem;
  /*width:100%;*/
  height:140px;
  /*min-width:510px;*/
  min-width: 338px;

  position: relative;
  border-radius: 6px;



    
}

.JobInfoComponent__container.dark .jobinfo-details,
.JobInfoComponent__container.dark .company-link
{
    background-color:#19202D;

}
.JobInfoComponent__container.dark .jobinfo-details .company-title{
    color:white;
}

.JobInfoComponent__container.dark .company-link button{
    background-color:#6779903b;
    color:white;
}
.company-name{
    background-color: yellow;
    display: flex;
    justify-content: center;
    align-items: center;
    

}


.jobinfo-details{
    display: grid;
    gap:0.7rem;
    padding:2rem;
    background-color: white;
   /* position: relative;*/
    
}
.company-link{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
  
}
.company-link button{
    padding:1rem 3rem;
    color:#5964E0;
    background-color:#F2F2F2;
    border: none;
    font-size: 1rem;
    font-weight: 700;
    margin-right:0.6rem;

}
.company-title{
    font-weight: 700;
    font-size: 1.5rem;
    
}
.company-site{
    color: #6E8098;
    font-weight:400;
}

.container-jobinfo{
    max-width: 520px;
    background-color: #5964E0;
}
.jobinfo__logo__container{
    background-color:gray;
    position:absolute;
   top:-60px;
    left:45%;
    padding:1rem 0.5rem;
    max-width: 50px;
    width:50px;
    height:50px;
    max-height: 50px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
    display: none;
}

